<template>
  <div class="post-archive">
    <h1 class="label-title" v-if="labelStr!=''">正在查看 {{labelStr}} 下的文章</h1>
    <div class="archive-item" v-for="item in archiveList"  @click="gotoRead(item.number)">
      <h2 v-if="item.fyear!=null">{{item.fyear}}</h2>
      <i class="fa fa-wpforms" aria-hidden="true"></i>
<!--      <span class="date">{{item.created_at | formatGithubTime}}</span>-->
      <span class="date">{{item.created_at | formatGithubTime}}</span>
      <span class="title">{{item.title}}</span>
    </div>
    <div v-show="archiveList==null || archiveList.length==0" style="color:#888;text-align: center">{{loadStatus}}</div>
<!--      <a-pagination size="small" :current="3" :total="50" @change="onChange" />-->

    <my-pagination :page-count="countBlog" @onPageSelect="onPageSelect"></my-pagination>
  </div>
</template>

<script>
  import {getBlogList,gitapiBlogList} from "../api/request";
  import MyPagination from "../components/MyPagination";
  import {pageStore} from "../store/pageStore";

  export default {
    name: "BlogFilter",
    data(){
      return {
        labelType:'android', //1:android
        labelStr:'Android',

        archiveList:null,
        test:'123',
        countBlog:1,
        pageIndex:1,
        loadStatus:'加载中...',
      }
    },
    components: {
      MyPagination
    },
    mounted() {
      if (this.$route.query.id){
        this.labelType = this.$route.query.id;
        if (this.$route.query.id == 'android'){
          this.countBlog = pageStore.pageState.android;
        }else if (this.$route.query.id == 'html5'){
          this.countBlog = pageStore.pageState.html5;
        }else if (this.$route.query.id == 'javaWeb'){
          this.countBlog = pageStore.pageState.javaWeb;

        }else if (this.$route.query.id == 'tools'){
          this.countBlog = pageStore.pageState.tools;

        }else if (this.$route.query.id == 'life'){
          this.countBlog = pageStore.pageState.life;

        }else if (this.$route.query.id == 'beyond'){
          this.countBlog = pageStore.pageState.beyond;

        }
      }
      if (this.$route.query.name){
        this.labelStr = this.$route.query.name;
      }

      if (this.$route.query.pageIndex){
        this.pageIndex = this.$route.query.pageIndex;
      }else{
        this.pageIndex = 0;
      }

      console.log(this.labelType+"---"+this.labelStr);
      this.getBlogsByType(this.pageIndex);


    },
    methods:{
      onChange(){

      },
      getBlogsByType(pageIndex){
        var _this = this;
        _this.loadStatus = '加载中...';
        gitapiBlogList(pageIndex,1,_this.labelType).then((response)=>{
            _this.archiveList = response;
            _this.formatBlogList();

            if (_this.archiveList.length == 0){
              _this.loadStatus = '没有更多数据';
            }
          // if (response.code==200){
          // }
        }).catch((err)=>{

        })
      },
      gotoRead(id){
        // this.$parent.currentContent = detail;
        // this.$parent.selectTab(10);
        // this.$router.push({name:"blogContent",params:{id:id}})
        this.$router.push({name:"blogContent",query:{id:id}})
      },
      onPageSelect(index){
        // var _this = this;
        // _this.$router.push({name:"blogArchive",query:{id:Number(_this.labelType),name:_this.labelStr,pageIndex:--index}})

        this.getBlogsByType(index); //选择大于1页的文章，跳转至详情页回跳时有问题
        console.log('parent-blogArchive-getPage:'+index)
      },
      formatBlogList(){
        var _this = this;
        if (_this.archiveList != null && _this.archiveList.length>0){
          for (let i = 0; i < _this.archiveList.length; i++) {
            if (i==0){
              _this.archiveList[i].fyear = _this.archiveList[i].created_at.substring(0,4);
            }else if (i>0){
              if (_this.archiveList[i].created_at.substring(0,4) != _this.archiveList[i-1].created_at.substring(0,4)){
                _this.archiveList[i].fyear = _this.archiveList[i].created_at.substring(0,4);
              }
            }

          }
        }
      },
    }
  }
</script>

<style scoped>
  /* for archive page starts*/
  .label-title {
    margin-top: 10px;
    font-size: 20px;
    font-weight: normal;
    color: #888;
  }
  .post-archive {
    font-size: 15px;
    line-height: 2;
    padding: 0.8rem 10px;
    text-align: start;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .post-archive .archive-item{
    color: #444;
    padding: 5px 10px;
  }
  .post-archive h2 {
    margin: 10px 5px;
    font: bold 25px / 1.1 "ff-tisa-web-pro", Cambria, "Times New Roman", Georgia, Times, sans-serif; }
  .post-archive .date {
    padding-left: 0.2em;
    padding-right: .5em;
    color: #444444;
  }
  .post-archive .title {
    color: #6E7173
  }
  /* for archive page ends*/

  #components-pagination-demo-mini .ant-pagination:not(:last-child) {
    margin-bottom: 24px;
  }
  .ant-pagination li{
    padding: 0;
  }
  .ant-pagination-item-active{
    /*color: #6E7173;*/
    border-color: #6E7173;
  }
  .ant-pagination-item-active {
    font-weight: 500;
    background: #fff;
    border-color: #6E7173;
  }
</style>



